@use 'helpers' as *;

.container{
max-width: calc(var(--container-width) + (var(--container-padding-x)*2));
margin-inline: auto;
padding-inline: var(--container-padding-x);
}

.visually-hidden{
@include visually-hidden;
}

.hidden-mobile{
    @include mobile{
        display: none !important
    }
}

.visible-mobile{
    @include mobile-above{
        display: none !important;
    }
}


.full-vw-line {
    position: relative;
  
    &::before,
    &::after {
      position: absolute;
      width: calc(
        (var(--100vw) - var(--container-width)) / 2
      );
      height: rem(1);
      background-color: var(--color-dark-15);
    }
  
    &--top {
      &::before {
        content: '';
        bottom: 100%;
      }
    }
  
    &--bottom {
      &::after {
        content: '';
        top: 100%;
      }
    }
  
    &--left {
      &::before,
      &::after {
        right: 100%;
      }
    }
  
    &--right {
      &::before,
      &::after {
        left: 100%;
      }
    }
  }

  .circle-icon{
    --cyrcleSize: #{rem(52)};
    --marginRightSize: #{rem(20)};

        min-height: var(--cyrcleSize);
        padding-right: calc(var(--cyrcleSize) + var(--marginRightSize));
        position: relative;

        @include laptop{
            --cyrcleSize: #{rem(44)};
            --marginRightSize: #{rem(14)};
        }

        @include tablet{
            --cyrcleSize: #{rem(40)};
        }

    &::after{
      @include square(var(--cyrcleSize));
      @include abs-y;
      content: '';
      right: 0;
      border-radius: 50%;
      background: 
      url('./../icons/black-top-rigth-arrow.svg') center no-repeat
       var(--color-accent);

   }
  }