  font-family: Kumbh Sans;
  src: url("KumbhSans-Regular.68a9e00f.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Kumbh Sans;
  src: url("KumbhSans-Regular.68a9e00f.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Regular.b9aebc2a.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Medium.12181361.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: url("Inter-SemiBold.db27060a.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Bold.df671d2b.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

.container {
  max-width: calc(var(--container-width)  + var(--container-padding-x) * 2);
  padding-inline: var(--container-padding-x);
  margin-inline: auto;
}

.visually-hidden {
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

@media (width <= 47.9988rem) {
  .hidden-mobile {
    display: none !important;
  }
}

@media (width > 47.9988rem) {
  .visible-mobile {
    display: none !important;
  }
}

.full-vw-line {
  position: relative;
}

.full-vw-line:before, .full-vw-line:after {
  width: calc((var(--100vw)  - var(--container-width)) / 2);
  background-color: var(--color-dark-15);
  height: .0625rem;
  position: absolute;
}

.full-vw-line--top:before {
  content: "";
  bottom: 100%;
}

.full-vw-line--bottom:after {
  content: "";
  top: 100%;
}

.full-vw-line--left:before, .full-vw-line--left:after {
  right: 100%;
}

.full-vw-line--right:before, .full-vw-line--right:after {
  left: 100%;
}

.circle-icon {
  --cyrcleSize: 3.25rem;
  --marginRightSize: 1.25rem;
  min-height: var(--cyrcleSize);
  padding-right: calc(var(--cyrcleSize)  + var(--marginRightSize));
  position: relative;
}

@media (width <= 90.0613rem) {
  .circle-icon {
    --cyrcleSize: 2.75rem;
    --marginRightSize: .875rem;
  }
}

@media (width <= 63.9988rem) {
  .circle-icon {
    --cyrcleSize: 2.5rem;
  }
}

.circle-icon:after {
  width: var(--cyrcleSize);
  height: var(--cyrcleSize);
  content: "";
  background: url("black-top-rigth-arrow.7454169f.svg") center no-repeat var(--color-accent);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  right: 0;
  translate: 0 -50%;
}

html.is-lock {
  overflow: hidden !important;
}

body {
  font-size: clamp(.875rem, .811275rem + .261438vw, 1.125rem);
  font-family: var(--font-family-base);
  letter-spacing: -.03em;
  color: var(--color-gray-50);
  background-color: var(--color-dark);
  flex-direction: column;
  display: flex;
}

main {
  flex-grow: 1;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  color: var(--color-light);
  font-weight: 500;
}

h1, .h1, h2, .h2, h3, .h3 {
  font-weight: 500;
}

h4, .h4, h5, .h5, h6, .h6 {
  font-family: var(--font-family-accent);
  font-weight: 600;
}

h1, .h1 {
  font-size: clamp(1.875rem, 1.23775rem + 2.61438vw, 4.375rem);
  line-height: 1.2;
}

h2, .h2 {
  font-size: clamp(1.75rem, 1.27206rem + 1.96078vw, 3.625rem);
  line-height: 1.3;
}

h3, .h3 {
  font-size: clamp(1.5rem, 1.2451rem + 1.04575vw, 2.5rem);
}

h4, .h4 {
  font-size: clamp(1.125rem, .997549rem + .522876vw, 1.625rem);
}

h5, .h5 {
  font-size: clamp(1.125rem, 1.02941rem + .392157vw, 1.5rem);
}

h6, .h6 {
  font-size: clamp(1rem, .936275rem + .261438vw, 1.25rem);
}

.text-18 {
  font-size: clamp(.875rem, .811275rem + .261438vw, 1.125rem);
}

a, button, textarea, label, input, select, svg * {
  transition-duration: var(--transition-duration);
}

a {
  color: inherit;
}

@media (any-hover: hover) {
  a:hover {
    color: var(--color-accent);
  }
}

@media (any-hover: none) {
  a:active {
    color: var(--color-accent);
  }
}

a[class] {
  text-decoration: none;
}

:focus-visible {
  outline: .125rem dashed var(--color-light);
  outline-offset: .25rem;
  transition-duration: 0s !important;
}

a[area-label], button[area-label] {
  position: relative;
}

a[area-label]:before, button[area-label]:before {
  content: "";
  width: 2.75rem;
  height: 2.75rem;
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}

:root {
  --100vw: 100vw;
  --color-light: #fff;
  --color-dark: #141414;
  --color-dark-10: #1a1a1a;
  --color-dark-15: #262626;
  --color-dark-20: #333;
  --color-dark-40: #666;
  --color-gray-50: #7e7e81;
  --color-gray-60: #98989a;
  --color-accent: #ffd11a;
  --color-red: #f50;
  --font-family-base: "Kumbh Sans", sans-serif;
  --font-family-accent: "Inter", sans-serif;
  --border: .0625rem solid var(--color-dark-15);
  --container-width: 100rem;
  --container-padding-x: 1rem;
  --border-gray-50: .0625rem solid var(--color-gray-50);
  --gradient: linear-gradient(45deg, var(--color-dark-15) 64%, var(--color-accent) 136%);
  --shadow: 0 0 0 .1875rem #191919;
  --transition-duration: .2s;
  --transparent: transparent;
}

@media (width <= 90.0613rem) {
  :root {
    --container-width: 80rem;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

:where(ul, ol):where([class]) {
  padding-left: 0;
}

body, :where(blockquote, figure):where([class]) {
  margin: 0;
}

:where(h1, h2, h3, h4, h5, h6, p, ul, ol, dl):where([class]) {
  margin-block: 0;
}

:where(dd[class]) {
  margin-left: 0;
}

:where(fieldset[class]) {
  border: none;
  margin-left: 0;
  padding: 0;
}

:where(ul[class]) {
  list-style: none;
}

:where(address[class]) {
  font-style: normal;
}

p {
  --paragraphMarginBottom: 24px;
  margin-block: 0;
}

p:where(:not([class]):not(:last-child)) {
  margin-bottom: var(--paragraphMarginBottom);
}

img, video {
  max-width: 100%;
  height: auto;
  display: block;
}

input, textarea, select, button {
  font: inherit;
}

html {
  scrollbar-gutter: stable;
  height: 100%;
}

html, :has(:target) {
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  line-height: 1.5;
}

a:where([class]) {
  display: inline-flex;
}

button, label {
  cursor: pointer;
}

:where([fill]:not([fill="none"], [fill^="url"])) {
  fill: currentColor;
}

:where([stroke]:not([stroke="none"], [stroke^="url"])) {
  stroke: currentColor;
}

svg * {
  transition-property: fill, stroke;
}

:where(table) {
  border-collapse: collapse;
  border-color: currentColor;
}

@media (prefers-reduced-motion: reduce) {
  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.expandable-content {
  --initialMaxHeight: 31.25rem;
  position: relative;
  overflow-y: hidden;
}

.expandable-content.is-expanded:before, .expandable-content.is-expanded .expandable-content__button {
  opacity: 0;
  visibility: hidden;
}

.expandable-content:not(.is-expanded) {
  max-height: var(--initialMaxHeight);
}

.expandable-content:before {
  content: "";
  pointer-events: none;
  width: 100%;
  height: 30%;
  transition-duration: var(--transition-duration);
  background-image: linear-gradient(#14141400 0%, #141414 77.38%);
  position: absolute;
  bottom: 0;
  left: 0;
}

.expandable-content__button {
  position: absolute;
  bottom: 4.25rem;
  left: 50%;
  translate: -50%;
}

.logo {
  max-width: clamp(7.875rem, 7.03064rem + 3.46405vw, 11.1875rem);
}

.icon {
  align-items: center;
  column-gap: .625rem;
  display: inline-flex;
}

.icon:after {
  --size: 1.5rem;
  width: var(--size);
  height: var(--size);
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  flex-shrink: 0;
}

.icon--yellow-arrow:after {
  background-image: url("yellow-top-right-arrow.6e008e10.svg");
}

@media (width <= 90.0613rem) {
  .icon--yellow-arrow {
    --size: 1.25rem;
  }
}

.icon--yellow-eye:after {
  background-image: url("3.950d45da.svg");
}

@media (width <= 90.0613rem) {
  .icon--yellow-eye {
    --size: 1.25rem;
  }
}

.icon--gray-arrow-down:after {
  background-image: url("gray-bottom-arrow.f988080d.svg");
}

@media (width <= 90.0613rem) {
  .icon--gray-arrow-down {
    --size: 1.25rem;
  }
}

.button {
  min-width: 8.5625rem;
  color: var(--color-gray-60);
  white-space: nowrap;
  background-color: var(--color-dark);
  border: var(--border);
  border-radius: .75rem;
  justify-content: center;
  padding: 1.125rem 1.5rem;
  font-size: 1.125rem;
  display: inline-flex;
}

@media (width <= 90.0613rem) {
  .button {
    border-radius: .5rem;
    min-width: 6.4375rem;
    padding: .875rem 1.25rem;
    font-size: .875rem;
  }
}

@media (any-hover: hover) {
  .button:hover {
    color: var(--color-accent);
    border: .0625rem solid var(--color-accent);
    background-color: var(--color-dark-10);
  }
}

@media (any-hover: none) {
  .button:active {
    color: var(--color-accent);
    border: .0625rem solid var(--color-accent);
    background-color: var(--color-dark-10);
  }
}

.button--accent {
  background-color: var(--color-accent);
  color: var(--color-dark);
  border-color: var(--color-accent);
  border-radius: .625rem;
  font-weight: 500;
}

@media (width <= 90.0613rem) {
  .button--accent {
    border-radius: .5rem;
    padding: .625rem .875rem;
  }
}

.button-dark-gray {
  background-color: var(--color-dark-10);
}

.badge {
  color: var(--color-light);
  background: linear-gradient(var(--color-dark-10), var(--color-dark-10)) padding-box, var(--gradient) border-box;
  border: .0625rem solid #0000;
  border-radius: .375rem;
  padding: clamp(.125rem, .0931373rem + .130719vw, .25rem) clamp(.625rem, .561275rem + .261438vw, .875rem);
  font-size: clamp(.75rem, .686275rem + .261438vw, 1rem);
  display: inline-flex;
}

.soc1al--3-cols {
  width: 100%;
  max-width: 19.25rem;
}

.soc1al--3-cols .soc1al-list {
  grid-template-columns: repeat(3, 1fr);
  row-gap: .625rem;
  display: grid;
}

@media (width <= 90.0613rem) {
  .soc1al--3-cols .soc1al-list {
    column-gap: .625rem;
  }
}

.soc1al--3-cols .soc1al-link {
  background-color: var(--color-dark-10);
  border: var(--border);
  border-radius: .375rem;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 3.75rem;
  display: flex;
}

@media (width <= 90.0613rem) {
  .soc1al--3-cols .soc1al-link {
    height: 3rem;
  }
}

@media (any-hover: hover) {
  .soc1al--3-cols .soc1al-link:hover {
    border-color: var(--color-accent);
  }
}

@media (any-hover: none) {
  .soc1al--3-cols .soc1al-link:active {
    border-color: var(--color-accent);
  }
}

.soc1al-list {
  align-items: center;
  column-gap: clamp(.875rem, .779412rem + .392157vw, 1.25rem);
  display: inline-flex;
}

.soc1al-link {
  --size: 1.5rem;
  color: var(--color-light);
  transition-duration: var(--transition-duration);
  width: var(--size);
  height: var(--size);
}

@media (any-hover: hover) {
  .soc1al-link:hover {
    color: var(--color-accent);
  }
}

@media (any-hover: none) {
  .soc1al-link:active {
    color: var(--color-accent);
  }
}

.soc1al-item {
  display: flex;
}

.metrics {
  --columns: 3;
}

.metrics--4-cols {
  --columns: 4;
  text-align: center;
}

@media (width > 63.9988rem) {
  .metrics--4-cols .metrics__item:nth-child(4n) {
    padding-right: 0;
  }
}

@media (width <= 63.9988rem) {
  .metrics--4-cols {
    --columns: 2;
    padding-inline: 0;
  }

  .metrics--4-cols .metrics__item {
    border-bottom: var(--border);
  }

  .metrics--4-cols .metrics__item:nth-child(odd) {
    padding-left: var(--container-padding-x);
  }

  .metrics--4-cols .metrics__item:nth-child(2n) {
    padding-right: var(--container-padding-x);
    border-right: none;
  }
}

.metrics__list {
  grid-template-columns: repeat(var(--columns), 1fr);
  font-family: var(--font-family-accent);
  align-items: start;
  display: grid;
}

.metrics__item {
  --paddingSize: 3.125rem;
  padding: var(--paddingSize);
  flex-direction: column-reverse;
  row-gap: .625rem;
  display: flex;
}

@media (width <= 90.0613rem) {
  .metrics__item {
    padding: 1.875rem 2.5rem;
  }
}

@media (width <= 63.9988rem) {
  .metrics__item {
    row-gap: 0;
    padding: 1.25rem;
  }
}

.metrics__item:not(:last-child) {
  border-right: var(--border);
}

.metrics__item:first-child {
  padding-left: 0;
}

.metrics__value {
  font-weight: 600;
}

.metrics__sign {
  color: var(--color-accent);
}

.team {
  --personImageSize: 3.75rem;
  display: flex;
}

@media (width <= 90.0613rem) {
  .team {
    --personImageSize: 3.125rem;
  }
}

@media (width <= 47.9988rem) {
  .team {
    --personImageSize: 2.5rem;
  }
}

.team .person {
  width: var(--personImageSize);
  border: .125rem solid var(--color-dark-40);
  background-color: var(--color-dark-15);
  border-radius: 50%;
}

@media (width <= 90.0613rem) {
  .team .person {
    border-width: .0625rem;
  }
}

.team .person:not(:first-child) {
  margin-left: calc(var(--personImageSize) / 4 * -1);
  transition-duration: var(--transition-duration);
}

@media (any-hover: hover) {
  .team .person:hover {
    border-color: var(--color-accent);
  }
}

@media (any-hover: none) {
  .team .person:active {
    border-color: var(--color-accent);
  }
}

.advantage-card {
  row-gap: 1.875rem;
  display: grid;
}

@media (width <= 90.0613rem) {
  .advantage-card {
    row-gap: 1.25rem;
  }
}

@media (width <= 47.9988rem) {
  .advantage-card {
    row-gap: .875rem;
  }
}

@media (width <= 90.0613rem) {
  .advantage-card__image {
    width: 2.5rem;
    height: auto;
  }
}

@media (width <= 47.9988rem) {
  .advantage-card__image {
    width: 1.875rem;
    height: auto;
  }
}

.advantage-card__link {
  row-gap: .25rem;
  display: grid;
}

@media (width <= 90.0613rem) {
  .advantage-card__link {
    row-gap: .125rem;
  }
}

.advantage-card__details {
  font-family: var(--font-family-accent);
  font-size: clamp(.875rem, .779412rem + .392157vw, 1.25rem);
}

.resources-preview {
  border-left: var(--border);
  background: url("hero-bg.ab0743fa.png") 0 0 / cover no-repeat;
  flex-direction: column;
  justify-content: end;
  align-items: start;
  gap: 1.875rem;
  padding-bottom: 5rem;
  padding-left: 5rem;
  font-family: Inter, sans-serif;
  display: flex;
}

@media (width <= 90.0613rem) {
  .resources-preview {
    gap: 1.25rem;
    padding-bottom: 3.125rem;
    padding-left: 3.125rem;
  }
}

@media (width <= 63.9988rem) {
  .resources-preview {
    width: 100vw;
    margin-left: calc(var(--container-padding-x) * -1);
    background-size: contain;
    border-left: none;
    padding: 6.25rem 2.5rem 2.5rem;
  }
}

.resources-preview__body {
  flex-direction: column;
  gap: .875rem;
  display: flex;
}

@media (width <= 90.0613rem) {
  .resources-preview__body {
    gap: .375rem;
  }
}

@media (width <= 47.9988rem) {
  .resources-preview__body {
    gap: .25rem;
  }
}

@media (width <= 30.0613rem) {
  .resources-preview__button {
    width: 100%;
  }
}

.resources-preview__team {
  border: .0625rem solid var(--color-dark-15);
  background-color: var(--color-dark-10);
  border-radius: 50px;
  padding: .625rem;
}

@media (width <= 90.0613rem) {
  .resources-preview__team {
    padding: .5rem;
  }
}

.burger-button {
  width: 2.125rem;
  height: 2.125rem;
  color: var(--color-light);
  transition-duration: var(--transition-duration);
  background-color: #0000;
  border: none;
  flex-direction: column;
  justify-content: space-between;
  padding: .53125rem .265625rem;
  display: inline-flex;
}

@media (any-hover: hover) {
  .burger-button:hover {
    color: var(--color-accent);
  }
}

@media (any-hover: none) {
  .burger-button:active {
    color: var(--color-accent);
  }
}

.burger-button.is-active .burger-button__line:first-child {
  transform-origin: 0;
  translate: .25em -.1em;
  rotate: 45deg;
}

.burger-button.is-active .burger-button__line:nth-child(2) {
  rotate: -45deg;
}

.burger-button.is-active .burger-button__line:last-child {
  width: 0;
}

.burger-button__line {
  background-color: currentColor;
  border-radius: .1875rem;
  width: 100%;
  height: .125rem;
}

.burger-button__line:last-child {
  align-self: end;
  width: .875rem;
}

.list:not(:last-child), .list__item:not(:last-child) {
  border-bottom: var(--border);
}

.tile {
  background-color: var(--color-dark-10);
  border: var(--border);
  border-radius: .75rem;
  flex-direction: column;
  align-items: start;
  row-gap: clamp(.25rem, 1.04575vw - .00490196rem, 1.25rem);
  padding: clamp(.625rem, .147059rem + 1.96078vw, 2.5rem) clamp(.625rem, .401961rem + .915033vw, 1.5rem);
  display: flex;
}

@media (width <= 90.0613rem) {
  .tile {
    border-radius: .625rem;
  }
}

.tag {
  background-color: var(--color-dark-20);
  font-size: clamp(.875rem, .811275rem + .261438vw, 1.125rem);
  font-family: var(--font-family-accent);
  color: var(--color-light);
  border-radius: .25rem;
  padding: .375rem .625rem;
  font-weight: 500;
}

@media (width <= 90.0613rem) {
  .tag {
    padding: .25rem .5rem;
  }
}

.card {
  display: grid;
}

@media (width > 63.9988rem) {
  .card {
    grid-template-columns: 37.5% auto;
  }
}

.card__preview, .card__body {
  padding: 5rem;
}

@media (width <= 90.0613rem) {
  .card__preview, .card__body {
    padding: 3.75rem;
  }
}

.card__preview {
  border-right: var(--border);
  flex-direction: column;
  justify-content: center;
  gap: clamp(1.25rem, .612745rem + 2.61438vw, 3.75rem);
  padding-left: 0;
  display: flex;
}

@media (width <= 63.9988rem) {
  .card__preview {
    border-bottom: var(--border);
    border-right: none;
  }
}

@media (width <= 47.9988rem) {
  .card__preview {
    padding-block: 3.125rem;
    padding-inline: 0;
  }
}

.card__preview-main {
  gap: clamp(1.5rem, 1.08578rem + 1.69935vw, 3.125rem);
  display: grid;
}

