@use '../helpers/' as *;

.feedback-form {
    display: grid;
    gap: fluid(50, 20) rem(50);
  
    @include mobile-s-above {
      grid-template-columns: repeat(2, 1fr);
    }
  
    &__cell {
      &--wide {
        grid-column: -1 / 1;
      }
    }
  
    &__actions {
      display: flex;
      justify-content: space-between;
      align-items: center;
      column-gap: 1rem;
      align-self: stretch;
  
      @include mobile-s {
        flex-direction: column;
        align-items: stretch;
        row-gap: rem(20);
      }
    }
  }